@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header{
    padding-top: 3rem;
   
    
}
.header-container{
    text-align: center;
    align-items:center;
}
/*--------------------------CTA style-----------------------------*/
.cta{
    display: flex;
    margin-top: 1.5em;
    justify-content: center;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

/*------------------------------------------------------------------------*/

.header-social-icon{
    display: flex;
    align-items:center;
    grid-gap:0.3rem;
    gap:0.3rem;
}
.img-social-scroll-content{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    color:var(--color-primary);
    align-items:center;
    grid-column-gap: 5rem;
    -webkit-column-gap: 5rem;
            column-gap: 5rem;
    margin:2rem 0;

}
.img-social-scroll-content>img{
    width:15rem;
    height:15rem;
    border-radius:50%;
}


/*----------------------------Scroll Down----------------------------------------*/
.scroll-down{
    -webkit-transform:rotate(90deg);
            transform:rotate(90deg);
    font-size:0.8rem;
 
}

