@media screen  and  (max-width:640px) {
    header{
        height:100vh;
    }

    .img-social-scroll-content{
        grid-template-columns: 1fr;    
        align-items:center;
        grid-row-gap: 1rem;
        row-gap: 1rem;
        
    }
    .scroll-down{
        display:none;

    }
    .img-social-scroll-content>img{
        width:10rem;
        height:10rem;
        border-radius:50%;
        margin-left: auto;
        margin-right: auto;
    }
    .header-social{
        text-align:center;
    }

}

nav{
    background:rgba(58, 186, 250, 0.3); 
    width: -webkit-fit-content; 
    width: -moz-fit-content; 
    width: fit-content;
    display: block;    
    position: fixed;
    z-index: 2;
    padding: 0.5rem 0.5rem;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom:2rem;
    display:flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
 
    
    
}
nav a{
    background: transparent;
    padding: 0.3rem;
    border-radius: 50%;
    display:flex;
    color: var(--color-white);

}
nav a:hover{
    background: rgba(119, 16, 16, 0.4);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-white);
}

