.experience-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
    transition: var(--transition);
}
.front-end,.back-end{
    background-color: var(--color-primary-variant);
    padding:2rem 6rem;
    border-radius:1.5rem ;
    place-items: center;
   
}  

.experience-container>div:hover{
    background-color:var(--color-bg-variant);
    border:1px solid var(--color-primary);
    cursor: pointer;

}
.experience-container>div h2{
     color: var(--color-primary);
     margin-bottom:1rem;
     text-align: center
     
}

.experience-content{
    display: grid;
    grid-row-gap: 2rem;
    row-gap: 2rem;  
    grid-column-gap: 4rem;  
    -webkit-column-gap: 4rem;  
            column-gap: 4rem;
    grid-template-columns: 1fr 1fr; 
    
}
.experience-article{
    display:flex;
    grid-gap:1rem;
    gap:1rem;
}
.patch-icon{
    margin-top:0.5rem ;
    color: var(--color-primary)
}

/* ----------------------------------------------------MEDIA QUERY FOR MOBILE------------------------------------------------------------ */

@media screen and (max-width:650px){
    .experience-container{
        grid-template-columns:1fr;
        grid-row-gap:0.5rem;
        row-gap:0.5rem;
        margin: 0rem 2rem;
      
    }

    .experience-content{
        grid-row-gap: 1rem;
        row-gap: 1rem;  
        grid-template-columns: 0.5fr;
        place-items: center;


    }
  
    .front-end,.back-end{
        width: 50%;
        place-items: center;
        background:transparent;

    }
}


.about-container{
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    margin:0rem auto;
  
}
.lottie-img{
    width: 90%;
    height: 90%;
}
.about-cards{
    display: grid;
    grid-template-columns: 1fr 1fr ;
    grid-gap: 1rem;
    gap: 1rem;   
}
.card-content{
    border: 1px solid blanchedalmond;
    padding:2rem;
    background-color:var(--color-bg-variant);
    display: grid;
    text-align: center;
    border:1px solid transparent;
    border-radius:1rem;
    transition:var(--transition)
 
}
.card-content:hover{
    background-color: transparent;
    border: var(--color-primary-variant);
}

.icon{
    align-items: center;
   
}
.about-me{
    margin:1rem;
}


