#contact{
    margin:10rem 0;
}
.contact-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}
.contact-option{
    display: grid;
    grid-template-columns: 1fr;
    padding:1rem 2rem;
    background-color:var(--color-primary-variant);
    border-radius: 2rem;
    grid-row-gap: -0.5rem;
    row-gap: -0.5rem;
}
.contact-icon{
    color: var(--color-primary);
}
.form{
    background: var(--color-primary-variant);
    border-radius: 2rem;
    padding: 1rem 1rem;
}
.form>input{
    display:block;
    width: 90%;
    height:3rem;
    margin: 1rem auto;
    border-radius: 2rem;
    text-indent: 2rem;  
    border: 1px solid var(--color-bg-variant);
    
}
input:focus{
    outline: 3px solid var(--color-primary)
}
textarea:focus{
    outline: 3px solid var(--color-primary)
}
.form>textarea{
    width: 90%;
    border-radius: 2rem;  
    margin: 1rem auto;
    margin-left: 1.5rem;
    padding: 1rem 1rem;
    overflow: hidden;
    resize: none;
}
.form>button{
    display: block;
    margin:0rem auto;
    background-color: var(--color-bg-variant);
}
/*------------------------------------------MEDIA QUERY(SMALL DEVICE)--------------------------------------------------- */
@media screen  and  (max-width:640px) {
    .contact-container{
        grid-template-columns: 1fr;
    }
    #contact{
        margin-top: 2rem;
    }
    .form>textarea{
        margin-left:1rem;
    }
}

#service{
    margin-top:6rem;

}
.service-container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:1rem;
    gap:1rem;
    transition:var(--transition);
}
.service-content{
    background-color: var(--color-primary-variant);
    padding: 1rem 2rem;
    border-radius:1rem;

}
.service-container>article:hover{
    background-color: var(--color-bg);
    border:1px solid var(--color-primary);

}
.article-title>div h3{
    text-align: start;
    color: var(--color-primary);

}
.article-title{
    display: flex;
    grid-gap:0.5rem;
    gap:0.5rem;
    padding: 1rem 1rem;
    background:var(--color-bg);
    border-radius:1rem;
  
}
.work-icon{
    margin-top:0.5rem;
    color: var(--color-primary);
}
.article-body{
    margin:1rem 0;
}
.article-body>p{
    margin:1rem 0;
}
.project>h3{
    color: var(--color-primary);
}
.project>ul li{
    margin:0.5rem 0;
}
/* ----------------------------------------------------MEDIA QUERY FOR MOBILE------------------------------------------------------------ */
@media screen and (max-width:650px) {
    .service-container{
        grid-template-columns: 1fr;
    }
}

*{
    margin: 0;
    padding: 0;
    outline: none;
    border: none;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg:#1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition:all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

}
html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}
body{
    font-family:"Poppins" , sans-serif;
    background-color:#1f1f38;
    background-color:var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(/Portofolio/static/media/bg-image.d9c121a9.jpg);
}

/*--------------------------------------------- General style-------------------------------------------------------- */
.container{
    width:75%;
    width:var(--container-width-lg);
    margin:0 auto;
}

h1,h2,h3,h4,h5,h6{
    font-weight: 500;
    margin:0
}
h1{
    font: size 2.5rem;
}
section{
    /* height: 100vh; */
    margin: 2rem 0;
}
section>h2,
section>h5{
    text-align:center;
    color:rgba(255, 255, 255, 0.6);
    color:var(--color-light);
}
section>h2{
    color:#4db5ff;
    color:var(--color-primary);
    margin-bottom:3rem;
}
.text-light{
    color:rgba(255, 255, 255, 0.6);
    color:var(--color-light);
}
.text-white{
    color:#fff;
    color:var(--color-white);
}
a{
    color:#4db5ff;
    color:var(--color-primary);
    transition:all 400ms ease;
    transition:var(--transition);
}
a:hover{
    color:#fff;
    color:var(--color-white);
}
.btn{
    width:-webkit-max-content;
    width:max-content;
    color:#4db5ff;
    color:var(--color-primary);
    display:inline-block;
    cursor: pointer;
    border:1px solid #4db5ff;
    border:1px solid var(--color-primary);
    padding: 0.5rem 1.2rem;
    border-radius:0.4rem;
    transition:all 400ms ease;
    transition:var(--transition);
}

.btn:hover{
    background-color:#fff;
    background-color:var(--color-white);
    color:#1f1f38;
    color:var(--color-bg);
    border-color:transparent ;

}
.btn-primary{
    color:#4db5ff;
    color:var(--color-primary);
    background:#1f1f38;
    background:var(--color-bg);
}

img{
    display:block;
    width:100%;
    object-fit: cover;
}

/*------------------------------------------MEDIA QUERY(MEDIAM DEVICE)--------------------------------------------------- */
@media screen and (max-width:1024px) {
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
    
}

/*------------------------------------------MEDIA QUERY(SMALL DEVICE)--------------------------------------------------- */
@media screen and (max-width:600px) {
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section>h2{
        margin-bottom: 2rem;
    }
    
}


